import React, { useState, useEffect } from 'react';
import JobsHeader from './JobsHeader';
import FilterSection from './FilterSection';
import JobsList from './JobsList';
import '../jobs.css';

const PersonalizedJobs = ({ onGoToProfile, onGoToHome, onLogout, onOpenJobs, ticketData }) => {
  const [allJobs, setAllJobs] = useState([]);
  const [filteredJobs, setFilteredJobs] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isPersonalized, setIsPersonalized] = useState(true);
  
  // Filter state - always initialized with ticketData
  const [filters, setFilters] = useState({
    company: '',
    location: ticketData?.location || '',
    experience: ticketData?.experience || '',
    remote: ticketData?.remoteWork || '',
    jobType: ticketData?.jobType || '',
    field: ticketData?.field || ''
  });


  useEffect(() => {
    // TODO: Load jobs from API
    setAllJobs([]);
    setFilteredJobs([]);
    setIsLoading(false);
  }, []);

  // Apply filters immediately when component mounts or ticketData changes
  useEffect(() => {
    if (ticketData && allJobs.length > 0) {
      console.log('PersonalizedJobs - Applying filters with ticketData:', ticketData);
      const newFilters = {
        company: '',
        location: ticketData.location || '',
        experience: ticketData.experience || '',
        remote: ticketData.remoteWork || '',
        jobType: ticketData.jobType || '',
        field: ticketData.field || ''
      };
      setFilters(newFilters);
      
      // Apply filters immediately
      setTimeout(() => {
        handleApplyFilters(newFilters);
      }, 100);
    }
  }, [ticketData, allJobs]);

  const applyPersonalizedFilter = () => {
    const userTicket = JSON.parse(localStorage.getItem('userTicket') || '{}');
    
    if (Object.keys(userTicket).length === 0) {
      setFilteredJobs([...allJobs]);
      return;
    }

    const filtered = allJobs.filter(job => {
      let matches = true;
      
      if (userTicket.location && job.location !== userTicket.location) {
        matches = false;
      }
      
      if (userTicket.experience && job.experience !== userTicket.experience) {
        matches = false;
      }
      
      if (userTicket.remoteWork && job.remote !== userTicket.remoteWork) {
        matches = false;
      }
      
      if (userTicket.jobType && job.type !== userTicket.jobType) {
        matches = false;
      }
      
      if (userTicket.field && job.field !== userTicket.field) {
        matches = false;
      }
      
      return matches;
    });

    setFilteredJobs(filtered);
  };

  const handleApplyFilters = (filtersToApply) => {
    console.log('PersonalizedJobs handleApplyFilters called with filters:', filtersToApply);
    console.log('allJobs length:', allJobs.length);
    let jobs = [...allJobs];

    // Apply search filter
    if (filtersToApply.search) {
      jobs = jobs.filter(job => 
        job.company.toLowerCase().includes(filtersToApply.search.toLowerCase())
      );
    }

    // Apply location filter
    if (filtersToApply.location) {
      jobs = jobs.filter(job => job.location === filtersToApply.location);
    }

    // Apply experience filter
    if (filtersToApply.experience) {
      jobs = jobs.filter(job => job.experience === filtersToApply.experience);
    }

    // Apply remote filter
    if (filtersToApply.remote) {
      jobs = jobs.filter(job => job.remote === filtersToApply.remote);
    }

    // Apply job type filter
    if (filtersToApply.jobType) {
      jobs = jobs.filter(job => job.type === filtersToApply.jobType);
    }

    // Apply field filter
    if (filtersToApply.field) {
      jobs = jobs.filter(job => job.field === filtersToApply.field);
    }

    console.log('PersonalizedJobs filtered jobs count:', jobs.length);
    setFilteredJobs(jobs);
  };

  const handleClearFilters = () => {
    setFilteredJobs([...allJobs]);
    if (isPersonalized) {
      applyPersonalizedFilter();
    }
  };

  const handleApplyToJob = (jobId) => {
    console.log('Applying to job:', jobId);
    // Add application logic here
  };

  return (
    <div>
      <JobsHeader 
        onGoToProfile={onGoToProfile}
        onGoToHome={onGoToHome}
        onLogout={onLogout}
        onOpenJobs={onOpenJobs}
      />
      
      <div className="container">
        <h1 className="page-title fade-in-up">Personalized Job Recommendations</h1>

        {/* Show filter criteria but not editable */}
        {isPersonalized && (
          <div className="personalized-filters-display">
            <h3>Your Job Preferences:</h3>
            <div className="filter-criteria">
              <div className="criteria-item">
                <strong>Location:</strong> {filters.location || 'Any'}
              </div>
              <div className="criteria-item">
                <strong>Experience:</strong> {filters.experience || 'Any'}
              </div>
              <div className="criteria-item">
                <strong>Remote Work:</strong> {filters.remote || 'Any'}
              </div>
              <div className="criteria-item">
                <strong>Job Type:</strong> {filters.jobType || 'Any'}
              </div>
              <div className="criteria-item">
                <strong>Field:</strong> {filters.field || 'Any'}
              </div>
            </div>
            <div className="filtered-indicator">
              <i className="fas fa-filter"></i> Showing personalized job recommendations based on your profile
            </div>
          </div>
        )}

        <JobsList 
          jobs={filteredJobs}
          onApplyToJob={handleApplyToJob}
          isLoading={isLoading}
        />
      </div>
    </div>
  );
};

export default PersonalizedJobs;
