import React, { useState } from 'react';

const RecruiterProfileCard = ({ 
  profileData, 
  onAvatarUpload, 
  onCoverUpload, 
  onRefreshProfile 
}) => {
  const [avatarFile, setAvatarFile] = useState(null);
  const [coverFile, setCoverFile] = useState(null);

  const handleAvatarUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (!file.type.startsWith('image/')) {
        alert('Please select an image file only');
        return;
      }

      if (file.size > 2 * 1024 * 1024) {
        alert('Image size too large. Please upload an image smaller than 2MB');
        return;
      }

      setAvatarFile(file);
      onAvatarUpload(file);
    }
  };

  const handleCoverUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (!file.type.startsWith('image/')) {
        alert('Please select an image file only');
        return;
      }

      if (file.size > 3 * 1024 * 1024) {
        alert('Image size too large. Please upload an image smaller than 3MB');
        return;
      }

      setCoverFile(file);
      onCoverUpload(file);
    }
  };

  const handleCoverClick = () => {
    document.getElementById('coverFileInput').click();
  };

  const handleAvatarClick = (event) => {
    event.stopPropagation();
    document.getElementById('avatarFileInput').click();
  };

  return (
    <div className="profile-card" id="profile-card" role="main" aria-label="User profile">
      <div 
        className="profile-cover" 
        onClick={handleCoverClick} 
        role="button" 
        aria-label="Click to upload cover photo" 
        tabIndex="0"
      >
        {coverFile && (
          <img 
            src={URL.createObjectURL(coverFile)} 
            alt="Cover" 
            style={{ display: 'block' }}
          />
        )}
        <div 
          className="profile-avatar" 
          id="profileAvatar" 
          onClick={handleAvatarClick} 
          role="button" 
          aria-label="Click to upload profile photo" 
          tabIndex="0"
        >
          {avatarFile ? (
            <img 
              src={URL.createObjectURL(avatarFile)} 
              alt="Profile" 
              style={{ display: 'block' }}
            />
          ) : (
            <i className="fas fa-user" id="avatarIcon" aria-hidden="true"></i>
          )}
          <div className="avatar-upload-overlay" aria-hidden="true">
            <div>Click to upload photo</div>
          </div>
        </div>
        <input 
          type="file" 
          id="avatarFileInput" 
          className="file-input" 
          accept="image/*" 
          onChange={handleAvatarUpload} 
          aria-label="Upload profile photo"
        />
        <input 
          type="file" 
          id="coverFileInput" 
          className="file-input" 
          accept="image/*" 
          onChange={handleCoverUpload} 
          aria-label="Upload cover photo"
        />
      </div>
      <div className="profile-info">
        <h2 className="profile-name" id="profileName">{profileData.name}</h2>
        <p className="profile-title" id="profileTitle">{profileData.title}</p>
        <div className="profile-stats" role="region" aria-label="Profile statistics">
          <div className="stat" role="group" aria-label="Jobs count">
            <div className="stat-number" id="jobsCount" aria-live="polite">
              {profileData.jobs.filter(job => job.status === 'Active').length}
            </div>
            <div className="stat-label">Active Job List</div>
          </div>
          <div className="stat" role="group" aria-label="Candidates count">
            <div className="stat-number" id="candidatesCount" aria-live="polite">
              {profileData.jobs.reduce((sum, job) => sum + job.applications, 0)}
            </div>
            <div className="stat-label">Candidates</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RecruiterProfileCard;
