import React, { useState, useEffect } from 'react';
import Header from './Header';
import ProfileCard from './ProfileCard';
import ResumeSection from './ResumeSection';
import EducationSection from './EducationSection';
import QualificationsSection from './QualificationsSection';
import ProjectsSection from './ProjectsSection';
import ApplicationsSection from './ApplicationsSection';
import JobDetailsModal from './JobDetailsModal';
import JobOffersSection from './JobOffersSection';
import PersonalInfoSection from './PersonalInfoSection';
import CandidateTicketSection from './CandidateTicketSection';
import { candidateAPI, jobsAPI } from '../services/apiService';

const UserPortal = ({ onOpenJobs, onLogout, onGoToHome, userId }) => {

  // State for profile data
  const [profileData, setProfileData] = useState({
    name: '',
    title: '',
    aiScore: 0,
    availabilityStatus: 'Available for opportunities',
    projectsCount: 0,
    applicationsCount: 0
  });

  // State for education
  const [educationData, setEducationData] = useState({
    institution: '',
    degree: '',
    graduationYear: ''
  });

  // State for qualifications
  const [qualificationsData, setQualificationsData] = useState({
    technicalSkills: '',
    languages: '',
    certifications: ''
  });

  // State for projects
  const [projects, setProjects] = useState([]);

  // State for applications
  const [applications, setApplications] = useState([]);

  // State for job offers
  const [jobOffers, setJobOffers] = useState([]);

  // State for viewing job details
  const [isJobModalOpen, setIsJobModalOpen] = useState(false);
  const [selectedJob, setSelectedJob] = useState(null);

  // State for personal info
  const [personalInfo, setPersonalInfo] = useState({
    fullName: '',
    email: '',
    phone: '',
    location: ''
  });

  // State for candidate ticket
  const [ticketData, setTicketData] = useState({
    location: '',
    experience: '',
    remoteWork: '',
    jobType: '',
    field: ''
  });

  // State for CV
  const [currentCvFile, setCurrentCvFile] = useState('No CV file');

  // Auto-save indicator state
  const [autoSaveIndicator, setAutoSaveIndicator] = useState({
    show: false,
    status: 'saving' // saving, saved, error
  });

  // Auto-save function
  const showAutoSave = (status = 'saving') => {
    setAutoSaveIndicator({ show: true, status });
    setTimeout(() => {
      setAutoSaveIndicator({ show: false, status: 'saved' });
    }, 2000);
  };

  // Event handlers
  const handleRefreshProfile = () => {
    console.log('Refreshing profile...');
    showAutoSave('saving');
  };

  const handleOpenJobs = (withTicketData = false) => {
    if (withTicketData) {
      onOpenJobs(ticketData);
    } else {
      onOpenJobs(null); // No ticket data for regular job list
    }
  };

  const handleLogout = () => {
    onLogout();
  };

  const handleAvatarUpload = (file) => {
    console.log('Avatar uploaded:', file.name);
    setCurrentCvFile(file.name);
    showAutoSave('saved');
  };

  const handleCoverUpload = (file) => {
    console.log('Cover uploaded:', file.name);
    showAutoSave('saved');
  };

  const handleToggleAvailability = (status) => {
    setProfileData(prev => ({
      ...prev,
      availabilityStatus: status
    }));
    showAutoSave('saved');
  };

  const handleCvUpload = (file) => {
    console.log('CV uploaded:', file.name);
    setCurrentCvFile(file.name);
    showAutoSave('saved');
  };

  const handleSaveEducation = (data) => {
    setEducationData(data);
    showAutoSave('saved');
  };

  const handleSaveQualifications = (data) => {
    setQualificationsData(data);
    showAutoSave('saved');
  };

  const handleAddProject = (project) => {
    setProjects(prev => [...prev, project]);
    setProfileData(prev => ({
      ...prev,
      projectsCount: prev.projectsCount + 1
    }));
    showAutoSave('saved');
  };

  const handleRefreshApplications = async () => {
    try {
      const id = userId || localStorage.getItem('userId');
      if (!id) return;
      // Fetch applications for the candidate
      const result = await candidateAPI.getMyApplications(id);

      // In parallel, try to fetch all jobs to enrich with title/company
      let jobIdToInfo = {};
      try {
        const jobsResp = await jobsAPI.getAllJobs();
        if (jobsResp.success && Array.isArray(jobsResp.data)) {
          jobsResp.data.forEach(j => {
            const key = Number(j.jobId ?? j.id);
            jobIdToInfo[key] = {
              title: j.title || '',
              company: j.company || ''
            };
          });
        }
      } catch (_) { /* non-fatal - fallback to ID */ }

      if (result.success && Array.isArray(result.data)) {
        const mapped = result.data.map(a => {
          const key = Number(a.jobId);
          const info = jobIdToInfo[key] || {};
          return {
            id: a.jobApplicationId || a.id,
            jobId: a.jobId,
            title: info.title || `Job #${a.jobId}`,
            company: info.company || '',
            status: a.status === 'PENDING' ? 'Applied' : a.status,
            date: ''
          };
        });
        setApplications(mapped);
      }
      showAutoSave('saved');
    } catch (e) {
      showAutoSave('error');
    }
  };

  const handleViewApplication = async (appId) => {
    const app = applications.find(a => a.id === appId);
    if (!app || !app.jobId) return;
    try {
      const jobsResp = await jobsAPI.getAllJobs();
      if (jobsResp.success && Array.isArray(jobsResp.data)) {
        const job = jobsResp.data.find(j => Number(j.jobId ?? j.id) === Number(app.jobId));
        if (job) {
          setSelectedJob(job);
          setIsJobModalOpen(true);
          return;
        }
      }
      // Fallback: open modal with minimal info so user still sees something
      setSelectedJob({
        id: app.jobId,
        title: app.title,
        company: '',
        description: 'Details unavailable. Please refresh jobs and try again.',
        ticket: {}
      });
      setIsJobModalOpen(true);
    } catch (e) {}
  };

  const handleWithdrawApplication = (appId) => {
    console.log('Withdrawing application:', appId);
    setApplications(prev => prev.filter(app => app.id !== appId));
    setProfileData(prev => ({
      ...prev,
      applicationsCount: Math.max(0, prev.applicationsCount - 1)
    }));
    showAutoSave('saved');
  };

  const handlePrepareForInterview = (appId) => {
    console.log('Preparing for interview:', appId);
  };

  const handleApplyAgain = (appId) => {
    console.log('Applying again:', appId);
  };

  // Job Offers handlers
  const handleRefreshOffers = () => {
    console.log('Refreshing job offers...');
    // TODO: Implement API call to fetch job offers
  };

  const handleAcceptOffer = (offerId) => {
    console.log('Accepting offer:', offerId);
    setJobOffers(prev => prev.map(offer => 
      offer.id === offerId ? { ...offer, status: 'Accepted' } : offer
    ));
    showAutoSave('saved');
  };

  const handleDeclineOffer = (offerId) => {
    console.log('Declining offer:', offerId);
    setJobOffers(prev => prev.map(offer => 
      offer.id === offerId ? { ...offer, status: 'Declined' } : offer
    ));
    showAutoSave('saved');
  };

  const handleViewOffer = (offerId) => {
    console.log('Viewing offer:', offerId);
  };

  const handleSavePersonalInfo = (data) => {
    setPersonalInfo(data);
    showAutoSave('saved');
  };

  const handleSaveTicket = async (data) => {
    console.log('handleSaveTicket called with data:', data);
    console.log('userId:', userId);
    
    if (!userId) {
      console.error('No user ID available');
      showAutoSave('error');
      return;
    }

    try {
      // Convert frontend data format to backend format
      const ticketData = {
        location: data.location,
        experience: data.experience,
        remote: data.remoteWork,
        jobType: data.jobType,
        field: data.field
      };

      const result = await candidateAPI.updateTicket(userId, ticketData);
      
      if (result.success) {
        setTicketData(data);
        // Update profile title with new field
        setProfileData(prev => ({
          ...prev,
          title: data.field || 'Software Developer'
        }));
        showAutoSave('saved');
      } else {
        console.error('Failed to update ticket:', result.message);
        showAutoSave('error');
      }
    } catch (error) {
      console.error('Error updating ticket:', error);
      showAutoSave('error');
    }
  };

  const handleGetJobsForMe = () => {
    console.log('Getting jobs for me with ticket data:', ticketData);
    handleOpenJobs(true); // Pass true to indicate we want ticket data
  };

  // Load candidate data when userId changes
  useEffect(() => {
    if (userId) {
      console.log('UserPortal useEffect - userId received:', userId);
      loadCandidateData(userId);
      // Also load applications list for this candidate
      handleRefreshApplications();
    } else {
      console.log('UserPortal useEffect - no userId provided');
    }
  }, [userId]);

  // Load candidate data from backend
  const loadCandidateData = async (candidateId) => {
    try {
      console.log('loadCandidateData called with candidateId:', candidateId);
      const result = await candidateAPI.getDetails(candidateId);
      console.log('loadCandidateData result:', result);
      if (result.success && result.data) {
        const candidate = result.data;
        console.log('loadCandidateData candidate data:', candidate);
        
        // Update profile data
        const newTitle = candidate.title || candidate.ticket?.field || 'Software Developer';
        console.log('Setting profile title to:', newTitle);
        setProfileData(prev => ({
          ...prev,
          name: candidate.firstName + ' ' + candidate.lastName,
          title: newTitle // Use title from user, fallback to field from ticket, then default
        }));

        // Update personal info
        setPersonalInfo({
          fullName: candidate.firstName + ' ' + candidate.lastName,
          email: candidate.email,
          phone: candidate.phoneNumber,
          location: candidate.ticket?.location || ''
        });

        // Update ticket data
        if (candidate.ticket) {
          setTicketData({
            location: candidate.ticket.location || '',
            experience: candidate.ticket.experience || 'No Experience',
            remoteWork: candidate.ticket.remote || '',
            jobType: candidate.ticket.jobType || '',
            field: candidate.ticket.field || ''
          });
        }

        // Update other data if available
        if (candidate.education) {
          setEducationData({
            institution: candidate.education,
            degree: '',
            graduationYear: ''
          });
        }

        if (candidate.qualifications) {
          setQualificationsData({
            technicalSkills: candidate.qualifications,
            languages: '',
            certifications: ''
          });
        }
      }
    } catch (error) {
      console.error('Error loading candidate data:', error);
    }
  };

  // Update applications count when applications change
  useEffect(() => {
    setProfileData(prev => ({
      ...prev,
      applicationsCount: applications.length
    }));
  }, [applications]);

  return (
    <div>
      <Header 
        onRefreshProfile={handleRefreshProfile}
        onOpenJobs={handleOpenJobs}
        onLogout={handleLogout}
        onGoToHome={onGoToHome}
      />
      
      <div className="container">
        <ProfileCard 
          profileName={profileData.name}
          profileTitle={profileData.title}
          aiScore={profileData.aiScore}
          availabilityStatus={profileData.availabilityStatus}
          projectsCount={profileData.projectsCount}
          applicationsCount={profileData.applicationsCount}
          onAvatarUpload={handleAvatarUpload}
          onCoverUpload={handleCoverUpload}
          onToggleAvailability={handleToggleAvailability}
        />

        <div className="main-content" id="main-content">
          <ResumeSection 
            currentCvFile={currentCvFile}
            onCvUpload={handleCvUpload}
          />
          
          <EducationSection 
            education={educationData.institution}
            degree={educationData.degree}
            graduationYear={educationData.graduationYear}
            onSaveEducation={handleSaveEducation}
          />

          <QualificationsSection 
            technicalSkills={qualificationsData.technicalSkills}
            languages={qualificationsData.languages}
            certifications={qualificationsData.certifications}
            onSaveQualifications={handleSaveQualifications}
          />

          <ProjectsSection 
            projects={projects}
            onAddProject={handleAddProject}
          />

          <PersonalInfoSection 
            fullName={personalInfo.fullName}
            email={personalInfo.email}
            phone={personalInfo.phone}
            location={personalInfo.location}
            onSavePersonalInfo={handleSavePersonalInfo}
          />

          <CandidateTicketSection 
            ticketData={ticketData}
            onSaveTicket={handleSaveTicket}
            onGetJobsForMe={handleGetJobsForMe}
          />

          <ApplicationsSection 
            applications={applications}
            onRefreshApplications={handleRefreshApplications}
            onViewApplication={handleViewApplication}
            onWithdrawApplication={handleWithdrawApplication}
            onPrepareForInterview={handlePrepareForInterview}
            onApplyAgain={handleApplyAgain}
          />

          {isJobModalOpen && selectedJob && (
            <JobDetailsModal 
              job={selectedJob}
              onClose={() => { setIsJobModalOpen(false); setSelectedJob(null); }}
            />
          )}

          <JobOffersSection 
            jobOffers={jobOffers}
            onRefreshOffers={handleRefreshOffers}
            onAcceptOffer={handleAcceptOffer}
            onDeclineOffer={handleDeclineOffer}
            onViewOffer={handleViewOffer}
          />
        </div>
      </div>

    </div>
  );
};

export default UserPortal;
