import React, { useState } from 'react';

const JobCard = ({ job, onApplyToJob, applicationStatus, index }) => {
  const [isExpanded, setIsExpanded] = useState(false);
  
  const truncateText = (text, maxLength = 120) => {
    if (!text || text.length <= maxLength) return text;
    return text.substring(0, maxLength) + '...';
  };

  const handleApply = () => {
    if (!applicationStatus) {
      onApplyToJob(job.id);
    }
  };

  return (
    <div 
      className="job-card fade-in-up" 
      style={{ animationDelay: `${index * 0.1}s` }}
    >
      <div className="job-header">
        <div>
          <h3 className="job-title">{job.title}</h3>
          <p className="job-company">{job.company}</p>
        </div>
        <span className="job-type-badge">{job.type}</span>
      </div>
      
      <div className="job-details">
        <div className="job-detail">
          <i className="fas fa-map-marker-alt"></i>
          <span>{job.location}</span>
        </div>
        <div className="job-detail">
          <i className="fas fa-home"></i>
          <span>Remote: {job.remote}</span>
        </div>
        <div className="job-detail">
          <i className="fas fa-tag"></i>
          <span>Field: {job.field}</span>
        </div>
        <div className="job-detail">
          <i className="fas fa-briefcase"></i>
          <span>Experience: {job.experience}</span>
        </div>
        <div className="job-detail">
          <i className="fas fa-clock"></i>
          <span>Job Type: {job.jobType}</span>
        </div>
      </div>
      
      <div className="job-description">
        <p>
          {isExpanded ? job.description : truncateText(job.description)}
        </p>
        {job.description && job.description.length > 120 && (
          <button 
            onClick={() => setIsExpanded(!isExpanded)}
            style={{
              background: 'none',
              border: 'none',
              color: '#007bff',
              cursor: 'pointer',
              fontSize: '14px',
              marginTop: '8px',
              textDecoration: 'underline',
              padding: '0'
            }}
          >
            {isExpanded ? 'Show Less' : 'Show More'}
          </button>
        )}
      </div>
      
      <div className="job-actions" style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
        {applicationStatus && (
          <span className="status-badge" style={{
            background: applicationStatus === 'PENDING' ? '#fff3cd' : '#e2e3e5',
            color: '#856404',
            padding: '6px 10px',
            borderRadius: '12px',
            fontSize: '12px',
            fontWeight: 600,
            border: '1px solid #ffeeba'
          }}>
            {applicationStatus === 'PENDING' ? 'Applied - Pending' : applicationStatus}
          </span>
        )}
        <button 
          className="btn btn-primary" 
          onClick={handleApply}
          disabled={!!applicationStatus}
          style={applicationStatus ? { opacity: 0.6, cursor: 'not-allowed' } : undefined}
        >
          <i className="fas fa-paper-plane"></i> {applicationStatus ? 'Applied' : 'Apply Now'}
        </button>
      </div>
    </div>
  );
};

export default JobCard;
