import React, { useState, useEffect } from 'react';

const ResumeSection = ({ currentCvFile, onCvUpload }) => {
  const [isUploadVisible, setIsUploadVisible] = useState(false);
  const [isDragOver, setIsDragOver] = useState(false);
  const [aboutMe, setAboutMe] = useState('');
  const [isEditingAboutMe, setIsEditingAboutMe] = useState(false);

  // Load about me from localStorage on component mount
  useEffect(() => {
    const savedAboutMe = localStorage.getItem('aboutMe');
    if (savedAboutMe) {
      setAboutMe(savedAboutMe);
    }
  }, []);

  const toggleCvUpload = () => {
    setIsUploadVisible(!isUploadVisible);
  };

  const handleFileSelect = (e) => {
    if (e.target.files && e.target.files[0]) {
      onCvUpload(e.target.files[0]);
    }
  };

  const handleDrop = (e) => {
    e.preventDefault();
    setIsDragOver(false);
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      onCvUpload(e.dataTransfer.files[0]);
    }
  };

  const handleDragOver = (e) => {
    e.preventDefault();
    setIsDragOver(true);
  };

  const handleDragLeave = (e) => {
    e.preventDefault();
    setIsDragOver(false);
  };

  const handleChooseFile = () => {
    document.getElementById('cvFileInput').click();
  };

  const toggleAboutMeEdit = () => {
    setIsEditingAboutMe(!isEditingAboutMe);
  };

  const handleAboutMeChange = (e) => {
    const value = e.target.value;
    if (value.length <= 300) {
      setAboutMe(value);
      // Auto-save to localStorage as user types
      localStorage.setItem('aboutMe', value);
    }
  };

  const saveAboutMe = () => {
    // Save to localStorage
    localStorage.setItem('aboutMe', aboutMe);
    setIsEditingAboutMe(false);
  };

  return (
    <div className="section-card">
      <div className="section-header">
        <h3 className="section-title">Resume</h3>
        <button 
          className="edit-btn" 
          onClick={toggleCvUpload} 
          aria-label="Upload CV file" 
          aria-expanded={isUploadVisible} 
          aria-controls="cvUploadArea"
        >
          <i className="fas fa-upload" aria-hidden="true"></i> Upload CV
        </button>
      </div>
      {isUploadVisible && (
        <div 
          className={`cv-upload ${isDragOver ? 'dragover' : ''}`} 
          id="cvUploadArea" 
          onDrop={handleDrop} 
          onDragOver={handleDragOver} 
          onDragLeave={handleDragLeave}
        >
          <div className="upload-icon">
            <i className="fas fa-file-pdf"></i>
          </div>
          <div className="upload-text">
            Drag and drop CV file here or click to select
          </div>
          <input 
            type="file" 
            id="cvFileInput" 
            className="file-input" 
            accept=".pdf,.doc,.docx" 
            onChange={handleFileSelect}
          />
          <button className="upload-btn" onClick={handleChooseFile}>
            Choose File
          </button>
        </div>
      )}
      <div id="cvStatus" className={!isUploadVisible ? 'hidden' : ''}>
        <div className="info-item">
          <div className="info-label">Current CV File:</div>
          <div className="info-value" id="currentCvFile">{currentCvFile}</div>
        </div>
      </div>
      
      {/* ABOUT ME Section */}
      <div className="about-me-section">
        <div className="about-me-header">
          <h4 className="about-me-title">About Me</h4>
          <button 
            className="edit-btn" 
            onClick={toggleAboutMeEdit} 
            aria-label="Edit About Me section" 
            aria-expanded={isEditingAboutMe} 
            aria-controls="aboutMeTextarea"
          >
            <i className="fas fa-edit" aria-hidden="true"></i> {isEditingAboutMe ? 'Cancel' : 'Edit'}
          </button>
        </div>
        
        {isEditingAboutMe ? (
          <div className="about-me-edit">
            <textarea
              id="aboutMeTextarea"
              className="about-me-textarea"
              value={aboutMe}
              onChange={handleAboutMeChange}
              placeholder="Tell us about yourself... Share your background, skills, interests, and what makes you unique."
              rows="4"
              maxLength="300"
            />
            <div className="character-counter">
              <span className={aboutMe.length > 270 ? 'warning' : ''}>
                {aboutMe.length}/300
              </span>
            </div>
            <div className="about-me-actions">
              <button className="save-btn" onClick={saveAboutMe}>
                <i className="fas fa-save" aria-hidden="true"></i> Save
              </button>
            </div>
          </div>
        ) : (
          <div className="about-me-content">
            {aboutMe ? (
              <p className="about-me-text">{aboutMe}</p>
            ) : (
              <p className="about-me-placeholder">Click "Edit" to add information about yourself</p>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default ResumeSection;
