import React, { useState } from 'react';
import '../recruiter-jobs.css';

const PublicJobCard = ({ job, hideApplyButton = false, onSignInClick }) => {
  const [isExpanded, setIsExpanded] = useState(false);
  
  const truncateText = (text, maxLength = 120) => {
    if (!text || text.length <= maxLength) return text;
    return text.substring(0, maxLength) + '...';
  };

  const getRemoteIcon = (remote) => {
    switch (remote) {
      case 'Yes':
        return <i className="fas fa-home"></i>;
      case 'No':
        return <i className="fas fa-building"></i>;
      case 'Hybrid':
        return <i className="fas fa-laptop-house"></i>;
      default:
        return <i className="fas fa-map-marker-alt"></i>;
    }
  };

  const getTypeColor = (type) => {
    switch (type) {
      case 'Full-time':
        return '#28a745';
      case 'Part-time':
        return '#ffc107';
      case 'Contract':
        return '#17a2b8';
      case 'Internship':
        return '#6f42c1';
      default:
        return '#6c757d';
    }
  };

  return (
    <div className="job-card">
      <div className="job-header">
        <div className="job-title-section">
          <h3 className="job-title">{job.title}</h3>
          <p className="job-company">{job.company}</p>
        </div>
        <div className="job-type-badge" style={{ backgroundColor: getTypeColor(job.type) }}>
          {job.type}
        </div>
      </div>

      <div className="job-details">
        <div className="job-location">
          <i className="fas fa-map-marker-alt"></i>
          <span>{job.location}</span>
        </div>
        <div className="job-remote">
          {getRemoteIcon(job.remote)}
          <span>{job.remote === 'Yes' ? 'Remote' : job.remote === 'No' ? 'On-site' : 'Hybrid'}</span>
        </div>
        <div className="job-field">
          <i className="fas fa-tag"></i>
          <span>{job.field}</span>
        </div>
        <div className="job-experience">
          <i className="fas fa-briefcase"></i>
          <span>Experience: {job.experience}</span>
        </div>
        <div className="job-type">
          <i className="fas fa-clock"></i>
          <span>Job Type: {job.jobType}</span>
        </div>
      </div>

      <div className="job-description">
        <p>
          {isExpanded ? job.description : truncateText(job.description)}
        </p>
        {job.description && job.description.length > 120 && (
          <button 
            onClick={() => setIsExpanded(!isExpanded)}
            style={{
              background: 'none',
              border: 'none',
              color: '#007bff',
              cursor: 'pointer',
              fontSize: '14px',
              marginTop: '8px',
              textDecoration: 'underline',
              padding: '0'
            }}
          >
            {isExpanded ? 'Show Less' : 'Show More'}
          </button>
        )}
      </div>

      <div className="job-footer">
        {!hideApplyButton && (
          <div className="job-actions">
            <button 
              className="btn btn-primary" 
              onClick={onSignInClick}
              style={{ 
                cursor: 'pointer'
              }}
            >
              <i className="fas fa-sign-in-alt"></i>
              Sign in to Apply
            </button>
          </div>
        )}
        <div className="job-meta">
        </div>
      </div>
    </div>
  );
};

export default PublicJobCard;
