import React, { useState } from 'react';

const EducationSection = ({ 
  education, 
  degree, 
  graduationYear, 
  onSaveEducation 
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [formData, setFormData] = useState({
    institution: education,
    degree: degree,
    graduationYear: graduationYear
  });

  const toggleEducationEdit = () => {
    setIsEditing(!isEditing);
    if (!isEditing) {
      setFormData({
        institution: education,
        degree: degree,
        graduationYear: graduationYear
      });
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSave = () => {
    onSaveEducation(formData);
    setIsEditing(false);
  };

  const handleCancel = () => {
    setFormData({
      institution: education,
      degree: degree,
      graduationYear: graduationYear
    });
    setIsEditing(false);
  };

  return (
    <div className="section-card">
      <div className="section-header">
        <h3 className="section-title">Education</h3>
        <button className="edit-btn" onClick={toggleEducationEdit}>
          <i className="fas fa-edit"></i> Edit
        </button>
      </div>
      {!isEditing ? (
        <div id="educationDisplay">
          <div className="info-item">
            <div className="info-label">Institution:</div>
            <div className="info-value" id="educationValue">{education || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Degree:</div>
            <div className="info-value" id="degreeValue">{degree || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Graduation Year:</div>
            <div className="info-value" id="graduationYearValue">{graduationYear || 'Not specified'}</div>
          </div>
        </div>
      ) : (
        <div id="educationEdit">
          <div className="form-group">
            <label className="form-label">Institution:</label>
            <input 
              type="text" 
              className="form-input" 
              id="educationInput" 
              name="institution"
              value={formData.institution}
              onChange={handleInputChange}
              placeholder="Enter institution name"
            />
          </div>
          <div className="form-group">
            <label className="form-label">Degree:</label>
            <input 
              type="text" 
              className="form-input" 
              id="degreeInput" 
              name="degree"
              value={formData.degree}
              onChange={handleInputChange}
              placeholder="Enter degree"
            />
          </div>
          <div className="form-group">
            <label className="form-label">Graduation Year:</label>
            <input 
              type="number" 
              className="form-input" 
              id="graduationYearInput" 
              name="graduationYear"
              value={formData.graduationYear}
              onChange={handleInputChange}
              placeholder="Graduation year"
            />
          </div>
          <div className="form-actions">
            <button className="btn btn-primary" onClick={handleSave}>Save</button>
            <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default EducationSection;
