import React, { useState } from 'react';

const ProjectsSection = ({ projects, onAddProject }) => {
  const [isAdding, setIsAdding] = useState(false);
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    githubLink: '',
    demoLink: ''
  });

  const toggleProjectsEdit = () => {
    setIsAdding(!isAdding);
    if (!isAdding) {
      setFormData({
        title: '',
        description: '',
        githubLink: '',
        demoLink: ''
      });
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleAddProject = () => {
    if (formData.title.trim()) {
      onAddProject(formData);
      setFormData({
        title: '',
        description: '',
        githubLink: '',
        demoLink: ''
      });
      setIsAdding(false);
    }
  };

  const handleCancel = () => {
    setFormData({
      title: '',
      description: '',
      githubLink: '',
      demoLink: ''
    });
    setIsAdding(false);
  };

  return (
    <div className="section-card">
      <div className="section-header">
        <h3 className="section-title">Projects</h3>
        <button className="edit-btn" onClick={toggleProjectsEdit}>
          <i className="fas fa-plus"></i> Add Project
        </button>
      </div>
      {!isAdding ? (
        <div id="projectsDisplay">
          {projects.length === 0 ? (
            <div className="empty-state">
              <i className="fas fa-folder-open"></i>
              <p>No projects added yet. Click "Add Project" to get started!</p>
            </div>
          ) : (
            projects.map((project, index) => (
              <div key={index} className="project-item">
                <div className="project-title">{project.title}</div>
                <div className="project-description">{project.description}</div>
                <div className="project-links">
                  {project.githubLink && (
                    <a href={project.githubLink} className="project-link" target="_blank" rel="noopener noreferrer">
                      <i className="fab fa-github"></i> GitHub
                    </a>
                  )}
                  {project.demoLink && (
                    <a href={project.demoLink} className="project-link" target="_blank" rel="noopener noreferrer">
                      <i className="fas fa-external-link-alt"></i> Live Demo
                    </a>
                  )}
                </div>
              </div>
            ))
          )}
        </div>
      ) : (
        <div id="projectsEdit">
          <div className="form-group">
            <label className="form-label">Project Name:</label>
            <input 
              type="text" 
              className="form-input" 
              id="projectTitleInput" 
              name="title"
              value={formData.title}
              onChange={handleInputChange}
              placeholder="Enter project name"
            />
          </div>
          <div className="form-group">
            <label className="form-label">Description:</label>
            <textarea 
              className="form-textarea" 
              id="projectDescriptionInput" 
              name="description"
              value={formData.description}
              onChange={handleInputChange}
              placeholder="Enter project description"
            />
          </div>
          <div className="form-group">
            <label className="form-label">GitHub Link:</label>
            <input 
              type="url" 
              className="form-input" 
              id="projectGithubInput" 
              name="githubLink"
              value={formData.githubLink}
              onChange={handleInputChange}
              placeholder="https://github.com/username/project"
            />
          </div>
          <div className="form-group">
            <label className="form-label">Live Demo Link:</label>
            <input 
              type="url" 
              className="form-input" 
              id="projectDemoInput" 
              name="demoLink"
              value={formData.demoLink}
              onChange={handleInputChange}
              placeholder="https://your-demo.com"
            />
          </div>
          <div className="form-actions">
            <button className="btn btn-primary" onClick={handleAddProject}>Add Project</button>
            <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default ProjectsSection;
